@charset "UTF-8";
@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://cdn.jsdelivr.net/npm/swiper@11.2.10/swiper-bundle.min.css");
/* CSS Document */

$containerWidth: 1300px;
$greenAccentColor: #008837;
$primaryBlackColor: #363535;
$font-title: "Oswald", sans-serif;
$font-text: "Roboto", serif;

html,
body {
	height: 100%;
}

body {
	font-family: $font-text;
	color: #363535;
	font-size: 16px;
	letter-spacing: 0;
	line-height: 140%;
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $font-title;
}

.noscroll {
	overflow: hidden;
}

a {
	text-decoration: none;
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;

	h1 {
		font-family: $font-title;
		font-weight: 900;
	}
}

.main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}

.footer__wrapper {
	border-bottom-left-radius: 50px;
	border-bottom-right-radius: 50px;
}

.container {
	max-width: $containerWidth;
	margin: 0 auto;
	padding: 10px;
}
/* .header__wrapper {
	position: sticky;
	top: 0;
} */

.header__wrapper .container {
	display: flex;
	flex-wrap: wrap;

	justify-content: space-between;
}

.header__top .container {
	display: flex;
	flex-wrap: wrap;

	justify-content: space-between;
}

.header {
	width: 100%;
	z-index: 9999;
	position: sticky;
	top: 0;
}

.overlay {
	position: fixed;
	top: 175px;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.8);
	z-index: 9998; /* Ниже хедера */
	opacity: 0;
	visibility: hidden;
	transition: opacity 0.3s ease, visibility 0.3s ease;
}

.overlay.active {
	opacity: 1;
	visibility: visible;
}

.logo-text {
	font-family: $font-text;
	font-weight: 500;
	font-style: Medium;
	font-size: 16px;
}

.nav-links,
.socials__wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
}

.nav-links {
	gap: 40px;
}

.socials__wrapper {
	gap: 17px;
}

.become-diller {
	border-radius: 5px;
	max-width: 162px;
	background-color: #3c3c3c;
	color: white;
	padding: 16px 20px;
	font-size: 14px;
	height: 33px;
	display: flex;
	text-align: center;
	justify-content: center;
	align-items: center;
}

.socials-mobile {
	display: none;
}

.logo {
	max-width: 256px;
	height: auto;
}

.header__bottom {
	background-color: #e7e7e7;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	.container {
		gap: 77px;
	}
}

.header__search {
	flex: 1;
}

.header__search {
	nav {
		height: 100%;
	}
	ul {
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		a {
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			li svg {
				transition: 0.3s ease;
			}
		}
	}
}

.header__search-input {
	position: relative;

	input {
		height: 36px;
		width: 100%;
		border-radius: 100px;
		background-color: #ffffff;
		padding: 7px 40px;
		z-index: 10;
	}

	img {
		position: absolute;
		height: 24px;
		width: 24px;
		top: 50%;
		transform: translateY(-50%);
		left: 1%;
		z-index: 0;
	}
}

.burger-menu {
	width: 24px;
	height: 24px;
	position: relative;
	background: none;
	border: none;
	cursor: pointer;
	padding: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	gap: 4px;
	transition: all 0.3s ease;
}

.burger-line {
	display: block;
	width: 100%;
	height: 2px;
	background-color: #333;
	border-radius: 1px;
	transition: all 0.3s ease;
	transform-origin: center;
}

.burger-menu.active .burger-line:nth-child(1) {
	transform: translateY(6px) rotate(45deg);
}

.burger-menu.active .burger-line:nth-child(2) {
	opacity: 0;
	transform: translateX(-10px);
}

.burger-menu.active .burger-line:nth-child(3) {
	transform: translateY(-6px) rotate(-45deg);
}

/* Ховер эффекты */
.burger-menu:hover .burger-line {
	background-color: #008837;
}

.logo-menu__wrapper {
	display: flex;
	align-items: start;
	justify-content: space-between;
}

.header__bottom-mobile {
	display: none;
	background-color: #e7e7e7;
	padding: 20px;
}

.header__burger {
	left: -200%;
	background-color: white;
	transition: transform 0.3s ease;
	position: absolute;
	/* position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%; */
}

.header__burger.active {
	left: 0;
	height: 190%;
	width: 100%;
	overflow: scroll;
	z-index: 999;
	border-bottom: 1px solid #c7c7c7;
}

.header__burger-container {
	padding-left: 10px;
	padding-right: 10px;
	padding-top: 20px;
	display: flex;
	height: 100%;
	flex-direction: column;
}

.header__burger-container ul {
	display: flex;
	flex-direction: column;
	gap: 20px;
	font-size: 18px;
}

.catalog-item_card-image {
	width: 200px;
	height: 200px;
	overflow: hidden;
	img {
		object-fit: contain;
		height: auto;
	}
}

.catalog-item_card {
	display: flex;
	flex-direction: column;
	gap: 10px;
	h2 {
		font-family: $font-title;
		font-weight: 600;
		line-height: 32px;
		letter-spacing: 0%;
	}
}

.catalog-item_card-content {
	display: flex;
	flex-direction: column;
	gap: 20px;
	align-items: flex-start;

	a {
		font-family: $font-text;
		font-weight: 400;
		font-style: Regular;
		font-size: 16px;

		line-height: 120%;
		letter-spacing: 0%;
	}
}

.dropdown-catalog-item,
.dropdown-docs-item {
	background-color: #ffffff;
	position: absolute;
	left: 0;
	transform: translateX(-100%);
	opacity: 0;
	visibility: hidden;
	transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
	width: 100%;
	/* max-width: $containerWidth; */
	margin: 0 auto;
	z-index: 1000;
}

.dropdown-docs-item-second {
	background-color: #ffffff;
	position: absolute;
	left: 0;
	transform: translateX(-100%);
	opacity: 0;
	visibility: hidden;
	transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
	width: 100%;
	/* max-width: $containerWidth; */
	margin: 0 auto;
	height: 100%;
	z-index: 1100;
}

.dropdown-catalog-item.active,
.dropdown-docs-item.active {
	transform: translateX(0);
	opacity: 1;
	visibility: visible;

	left: 50%;
	transform: translateX(-50%) translateY(0);
}

.dropdown-docs-item-second.active {
	transform: translateX(0);
	opacity: 1;
	visibility: visible;

	left: 50%;
	transform: translateX(-50%) translateY(0);
}

.dropdown-catalog-item,
.dropdown-docs-item {
	grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
}

.catalog-item_card.mobile {
	flex-direction: row;

	.catalog-item_card-image {
		width: 100px;
		height: 100px;
	}
}

.catalog-button-burger,
.docs-button-burger {
	display: flex;
	flex-direction: column;
	align-items: start;
	gap: 20px;
	max-height: 0;
	overflow: hidden;
	transition: max-height 0.3s ease;
}

#header-burger ul {
